import html2canvas from 'html2canvas'
import Indents from '../../api/indents'
import tTable from '@/components/t-table/t-table.vue'
import tTh from '@/components/t-table/t-th.vue'
import tTr from '@/components/t-table/t-tr.vue'
import tTd from '@/components/t-table/t-td.vue'
export default {
	components:{
		tTable,
		tTh,
		tTr,
		tTd
	},
	data() {
		return {
			tableData:[], 
			loading: false,
			indent: {},
			total: 0,
			paid: 0,
			imgUrl: ''
		}
	},
	onLoad(option){
		if(option.id){
			this.id = option.id
		}else{
			uni.showToast({
			    title: '参数有误',
			    duration: 2000
			})
			setTimeout(()=>{
				uni.navigateBack()
			}, 2000)
			return false
		}
		this.getList()
	},
	methods: {
		generate(){
			this.loading = true
			uni.showLoading({
			    title: '生成中'
			});
			setTimeout(()=>{
				this.toImage()
			}, 2000)
		},
		toImage() {
			html2canvas(this.$refs.imageWrapper,{
				y: 0,
				useCORS: true
			}).then(canvas => {
				let dataURL = canvas.toDataURL("image/png")
				this.imgUrl = dataURL;
				console.log(this.imgUrl)
				if (this.imgUrl !== "") {
					 this.loading = false
					 uni.hideLoading()
					 uni.previewImage({
						urls: [dataURL],
						longPressActions: {
							itemList: ['发送给朋友', '保存图片', '收藏'],
							success: function(data) {
								console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
							},
							fail: function(err) {
								console.log(err.errMsg);
							}
						}
					});
				}
			})
		},
		previewImage(){
			uni.previewImage({
				urls: [this.imgUrl],
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		async getList(){
			const that = this
			await Indents.getDetails(this.id,{},function(res){
				
				for(var k in res.goods_list){
					if(res.goods_list[k].good_sku){
						res.goods_list[k].good_sku.product_sku.forEach(item=>{
							if(res.goods_list[k].specification){
								res.goods_list[k].specification+= item.value + ';'
							}else{
								res.goods_list[k].specification = item.value + ';'
							}
							
						})
						res.goods_list[k].specification = res.goods_list[k].specification.substr(0,res.goods_list[k].specification.length-1)
					}
				}
				that.indent = res
				that.calcTotal()
				
				
			})
			
		},
		//计算总价
		calcTotal(){
			let list = this.indent
			
			if(list.goods_list.length === 0){
				return
			}
			let total = 0 //总费用
			let paid = 0 //已付
			if(list.good_indent_finance.length>0){
				list.good_indent_finance.forEach(item=>{
					if(item.type === 0){
						paid += item.money
					}
				})
			}
			list.goods_list.forEach(item=>{
				total += item.price * item.number
			})
			this.total = Number(total.toFixed(2))
			this.paid = Number(paid.toFixed(2))
		}
	}
}
